.Cart_table .table_title h2 {
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
}

.Cart_table img {
  width: 45px;
  height: 45px;
}

.Cart_table h3 {
  font-size: 17px;
  font-weight: 500;
}

.Cart_table h4 {
  font-size: 15px;
  font-weight: 500;
}

.Cart_table input {
  width: 60px;
  text-align: center;
}

.Cart_table i {
  color: #ffa400;
  font-weight: bold;
  font-size: 18px !important;
  cursor: pointer;
}

.clear_cart {
  border: 2px solid #ffa400;
  border-radius: 5px;
  color: #ffa400;
  font-size: 18px;
  cursor: pointer;
  font-weight: 400;
  padding: 1px 23px;
}

.total_cost {
  font-weight: bold;
  font-size: 17px;
  font-style: italic;
}

.master_card {
  height: 80px;
  width: 140px;
  margin-top: 10px;
  margin-bottom: 30px;
}

/* Media query */
@media (max-width: 992px) {
  .Cart_table .table_title {
    display: none !important;
  }

  .cart_Item {
    margin-bottom: 30px !important;
    border: 1px solid #dee2e6;
    padding: 15px 0;
    box-shadow: 10px 0.1rem 1rem rgba(0, 0, 0, 0.15) !important;
    border-radius: 0.25rem;
  }

  .Cart_table img {
    width: 150px;
    height: 150px;
  }

  .Cart_table h3 {
    font-size: 18px;
    padding: 5px 0;
  }

  .Cart_table h4 {
    font-size: 16px;
  }

  .Cart_table input {
    margin: 7px 0;
  }
}
